.welcome-section {
 @include handleFlexColumnCenter();
 background-image: url("../image/main-image.jpg");
 background-size: cover;
 background-repeat: no-repeat;
 background-position-x: 36%;
 min-height: 76vh;
 text-align: center;

 &__title {
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2.2rem;
 }
}

@media (min-width: 400px) {
 .welcome-section {
  &__title {
   font-size: 2.8rem;
  }
 }
}

@media (min-width: 1024px) {
 .welcome-section {
  &__title {
   font-size: 3.2rem;
   letter-spacing: 2px;
  }
 }
}

@media(min-width: 1350px) {
 .welcome-section {

  &__title {
   font-size: 3.5rem;
   letter-spacing: 4px;
  }
 }
}