.footer {
 @include handleFlexColumnCenter();
 min-height: 12vh;
 text-align: center;
 font-size: 1.2rem;
}

.copyright {
 &__text {
  margin: 0;
 }

 &__text:first-child {
  margin-bottom: 2px;
 }
}

@media(min-width: 400px) {
 .footer {
  font-size: 1.4rem;
 }
}

@media(min-width: 1024px) {
 .footer {
  font-size: 1.5rem;
 }
}

@media(min-width: 1440px) {
 .footer {
  font-size: 1.6rem;
 }
}